/* General */

@font-face {
	font-family: "Lemonade Stand";
	src: url("./lemonade_stand.otf");
}

body {
	box-sizing: border-box;
	margin: 0;
	font-family: "Lemonade Stand";
	background-color: #945a83;
}

/* Main */ 

.main {
	height: 100vh;
	width: 100vw;display: flex;
	flex-direction: column;
	text-align: center;
	align-items: center;
}

.title {
	margin: 140px 0 0 0;
	font-size: 50px;
	color: #fefffe;
	
}

.sub-title {
	margin: 0;
	font-size: 70px;
	color: #feca07;
	text-decoration: underline;
}

.main .card {
	position: absolute;
	top: 35%;
	width: 40%;
	height: 40%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	background-color: white;
	border-style: none none none solid;
	border-width: 10px;
	border-radius: 2px;
	border-color: #feca07;
}

.main .card .setup {
	margin: 20px 30px;
}

.main .card .punchline {
	margin: 18px 24px;
}

.main .card button {
	font-size: 18px;
	font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
	padding: 8px 16px;
	background-color: #945a83;
	color: white;
	border: 2px solid grey;
	border-radius: 8px;
	cursor: pointer;
	transition: all 0.1s ease-in-out;
}
	  
.main .card button:hover {
	background-color: #feca07;
	color: #945a83;
}

.img-one, .img-two {
	position: absolute;
}

.img-one {
	width: 400px;
	transform: translate(100%, 90%);
}

.img-two {
	width: 450px;
	transform: translate(-80%, 205%);
}